import { Routes } from '@angular/router';
import { ErrorComponent } from './error/error.component';


export const ROUTES: Routes = [{
   path: '', redirectTo: 'app', pathMatch: 'full'
  },
  {
    path: 'app',   loadChildren: () => System.import('.\\layout\\layout.module')  .then(function(module) {    return module['LayoutModule'];  })
  },
  {
    path: 'login', loadChildren: () => System.import('.\\login\\login.module')  .then(function(module) {    return module['LoginModule'];  })
  },
  {
    path: 'error', component: ErrorComponent
  },
  {
    path: '**',    component: ErrorComponent
  }
];



